@-ms-viewport {
  width: device-width; }

.wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  padding: 100px 0; }

.ycont {
  display: flex;
  flex-direction: column;
  max-width: 1400px;
  width: 100%;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  position: relative; }

@media screen and (max-width: 720px) {
  .wrapper {
    padding: 15px 0; } }
