.left {
    text-align: left; }
.color-white {
    color: $white; }
.accent {
    color: $accent;
    text-shadow: 0px 0px 40px rgba(133, 223, 252, 0.4); }
.bef {
    position: absolute; }
.main-first {
    position: relative;
    .cover-bg {} }
@media screen and (max-width: 768px) {
    h1 {
        font-size: 42px; }
    h2 {
        font-size: 36px; }

    h3 {
        font-size: 18px; }
    html {
        font-size: 16px; }

    .wrapper {
        padding: 50px 0; }
    .large {
        font-size: 18px; } }
